.film-form{
	margin-top: 30px;
	border: 2px solid #eee;
	padding-bottom: 10px;
}

.film-form__header{
	text-align: center;
	font-size: 22px;
	text-transform: uppercase;
	margin-top: 15px;
	margin-bottom: 20px;
}

.film-form__group{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	padding: 5px 10px;
}

.film-form__group--txtarea{
	-ms-flex-align: start;
	    align-items: flex-start;
}

.film-form__label{
	-ms-flex-preferred-size: 30%;
	    flex-basis: 30%;
}

.film-form__input{
	-ms-flex-preferred-size: 70%;
	    flex-basis: 70%;
	padding: 5px 10px 5px 15px;
}

.film-form__description{
	height: 200px;
	resize: none;
	padding-left: 5px;
}

.film-form__button{
	background-color: transparent;
	border: 2px solid #000;
	color: #000;
	margin-left: 50%;
	margin-top: 10px;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	font-weight: bold;
	font-size: 18px;
	padding: 5px 10px;
	text-transform: uppercase;
	-webkit-transition: background-color .25s ease, color .25s ease;
	-o-transition: background-color .25s ease, color .25s ease;
	transition: background-color .25s ease, color .25s ease;
}

.film-form__button:hover{
	display: inline-block;
	background-color: #000;
	color: #fff;

}


