
.app__header{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 10px 20px;
  border-bottom: 1px solid #e6e6e6;
}

.app__logo{
  width: 80px;
}

.app__nav{
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
}

.app__nav-itm{
  margin-right: 5px;
  padding: 5px 10px;
  text-transform: uppercase;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  -webkit-transition: border .25s ease;
  -o-transition: border .25s ease;
  transition: border .25s ease;
}

.app__nav-itm:hover{
  border-color: #333;
}

.app__nav-itm:last-child{
  margin-right: 0;
}

.app__main{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 20px;
}


