.film__item{
	margin-left: 1%;
	position: relative;
	background-color: #fafafa;
	border-radius: 5px;
	-ms-flex-preferred-size: 32%;
	    flex-basis: 32%;
	margin-bottom: 1%;
	padding-bottom: 10px;
}


.film__remove{
	position: absolute; 
	top: 5px;
	right: 5px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	border: 1px solid red;
	cursor: pointer;
	color: red;
	font-size: 14px;
	-webkit-transition: color .25s ease, background-color .25s ease;
	-o-transition: color .25s ease, background-color .25s ease;
	transition: color .25s ease, background-color .25s ease;
}

.film__remove:hover{
	color: #fff;
	background-color: red;

}

.film__title{
	padding: 10px 30px;
	
	font-size: 20px;
	border-bottom: 1px solid #fff;
}

.film__description{
	padding: 5px 20px;
}

.film__raiting{
	padding-left: 10px;
}

.film__raiting span{
	color: red;
}

.film__genres{
	display: -ms-flexbox;
	display: flex;
	margin-top: 10px;
}

.film__genre{
	cursor: default;
	margin-left: 5px;
	background-color: blue;
	color: #fff;
	padding: 5px 5px;
}


